.drift-box {
  width: 90%;
  height: 60vh;
  background-image: url('./Images/karl-greif-H5IXIH254AU-unsplash.jpg');
  background-size: cover;
  background-position: center;
  position: relative;
  left: 0;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  border-radius: 15px;
  margin: 0 auto;
  margin-bottom: 20px;
  transition: transform 0.3s ease, text-decoration 0.3s ease;
}

.drift-box:hover {
  transform: scale(1.05);
}

.drift-box:hover .open-table-link {
  text-decoration: underline;
  color: #ffcc00;
}

.text-container-drift {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 20px;
}

.drift-text {
  font-size: 20px;
  font-weight: bold;
  color:black;
  margin-bottom: 10px; /* Adjust the margin to control spacing */
  line-height: 0.7;
}

.open-table-link {
  font-size: 1.2rem;
  font-weight: bold;
  color: black;
  font-size: 20px;
  /* Add any other styles you want for the additional text here */
}
