/* SAM.css */
.SAM-container {
  margin: 0 auto;
  margin-bottom: 50px;
  /* Add more styles as needed */
  }
  
  .image-container {
    display: flex;
    justify-content: space-between;
    margin-top: 0px;
    margin-bottom: 0px;
  }
  
  .image-item-sam {
    flex: 2;
    margin-right: 10px;
  }
  
  .image-item:last-child {
    margin-right: 0;
  }
  
  .image-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 7px;
  }
  
  .text-container-s {
    margin-top: 20px;
    text-align: center;
    margin-left: 100px;
  }
  
  .big-bold-text {
    font-size: 20px;
    font-weight: bold;
  }
  
  .small-bold-text {
    font-size: 15px;
    font-weight: bold;
  }

  .table-container {
    margin-top: 20px;
    width: 70%; /* Adjust the width as needed */
    margin-left: auto;
    margin-right: auto;
    border-radius: 8px; /* Rounded outer table border */
    overflow: hidden; /* Hide the border-radius overflow */
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
    border-radius: 8px;
  }
  
  th, td {
    border: 1px solid #f0a1a1;
    padding: 8px;
    text-align: center; /* Rounded inner table borders */
  }
  
  th {
    background-color: #f6dfdf; /* Black background for headers */
    color: #000000; /* White text for headers */
  }
  
  .bottom-images-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .bottom-image-item-sam {
    margin: 0 10px;
  }
  
  .bottom-image-item-sam img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .model-result {
    font-size: 20px;
    color: black;
    font-weight: bold;
    margin-bottom: 20px; /* Adjust the margin to control spacing */
    line-height: 0.7;
    margin-top: 5px;
    margin-bottom: 20px;
    margin-left: 30%;
    margin-right: 40%;
    font-family: 'Times New Roman', Times, serif;
  }

  .model-title {
    font-size: 25px;
    margin-top: 35px;
    margin-left: 5%;
    font-weight: bold;
    font-family: 'Times New Roman', Times, serif;
  }

  .description-text {
    font-size: 20px;
    margin-top: 10px;
    margin-left: 5%;
    margin-bottom: 20px;
    font-family: 'Times New Roman', Times, serif;
  }